<template>
  <div class="handleTask">
      <DialogInput 
      :dialogTit="dialogTit" :isOpenDialog="isOpenDialog" :isEdit="isEdit" 
      @closeDialog="closeDialog" @editBtn="editBtn" @resetBtn="resetBtn" @saveBtn="saveBtn">
        <div slot="content">
           <form action="">
              <mu-text-field v-model="task.content" label="任务内容" :error-text="errorText[0]" color="primary" :rows="2" multi-line  :disabled="isDisabled" label-float full-width></mu-text-field><br/>

              <mu-radio :value="rank.rankNum" v-model="task.rank.rankNum" :label="rank.rankName" :key="rank.rankName" v-for="(rank,index) in taskRank" :disabled="isDisabled" color="primary"></mu-radio><br/>
              <mu-radio :value="theType.typeNum" v-model="task.type.typeNum" :label="theType.typeName" :key="theType.typeName" v-for="(theType,index) in taskType" :disabled="isDisabled" color="primary"></mu-radio>

              <mu-date-input v-model="task.startDate" label="开始日期" :error-text="errorText[1]" color="primary" display-color="primary" underline-color="primary" :disabled="isDisabled" label-float full-width></mu-date-input><br/>
              <mu-date-input v-model="task.endDate" label="截止日期" :error-text="errorText[2]" color="primary" display-color="primary" underline-color="primary" :disabled="isDisabled" label-float full-width></mu-date-input><br/>
              
             <mu-select label="执行者" v-model="task.executor" :error-text="errorText[3]" color="primary" filterable chips :disabled="isDisabled || executorDisabled" full-width>
                <mu-option v-for="member,index in members" :key="member.id" :label="member.name" :value="member" color="primary"></mu-option>
              </mu-select>
              
             <mu-select label="参与者" v-model="task.participant" color="primary" multiple filterable chips :disabled="isDisabled" full-width>
                <mu-option v-for="member,index in members" :key="member.id" :label="member.name" :value="member" color="primary"></mu-option>
              </mu-select>

              <mu-text-field v-model="task.remark" label="备注" color="primary" :rows="2" multi-line :disabled="isDisabled" label-float full-width></mu-text-field><br/>
           </form>
        </div>
      </DialogInput>
  </div>
</template>

<script type="text/ecmascript-6">

import DialogInput from "../dialog/DialogInput.vue";
import { mapGetters, mapMutations , mapActions } from "vuex";

export default {
  name: "handleTask",
  data() {
    return {
      executorDisabled:false,
      taskRank:[
        {
          rankNum:0,
          rankName:"普通"
        },
        {
          rankNum:1,
          rankName:"重要"
        },
        {
          rankNum:2,
          rankName:"紧急"
        }
      ],
      taskType:[
        {
          typeNum:0,
          typeName:"待办"
        },
        {
          typeNum:1,
          typeName:"进行中"
        },
        {
          typeNum:2,
          typeName:"待审核"
        },
        {
          typeNum:3,
          typeName:"已完成"
        }
      ],
      errorText:{
        0:'',
        1:'',
        2:'',
        3:''
      }
    };
  },
  computed:{
    ...mapGetters([
      'proList',
      'currentProId'
    ])
  },
  props: {
    isOpenDialog: {
      type: Boolean,
      default: false
    },
    dialogTit: {
      type: String
    },
    task: {
      type: Object,
      default: () => {
        return {
          id:-1,
          content: "",
          rank:
            {
              rankNum:0,
              rankName:"普通"
            },
          type:
            {
              typeNum:0,
              typeName:"待办"
            },
          startDate: "",
          endDate:"",
          executor:[],
          participant:[],
          remark:"",
          checked:false
        };
      }
    },
    isEdit:{
      type:Boolean,
      default:false
    },
    isDisabled:{
      type: Boolean,
      default: false
    },
    members:{
      type: Array,
      default:()=>{
        return []
      }
    }
  },
  components: {
    DialogInput
  },
  methods: {
    closeDialog() {
      this.$emit("closeDialog");

      this.emptyErrorText()
    },
    editBtn(){
      if(this.task.type.typeNum === 3){
        return
      }else{
        this.$emit("editBtn");
      }
    },
    resetBtn(){
        this.task.id=-1
        this.task.content=""
        this.task.rank={
            rankNum:0,
            rankName:"普通"
          }
        this.task.type={
            typeNum:0,
            typeName:"待办"
          }
        this.task.startDate= ""
        this.task.endDate=""
        this.task.executor=[]
        this.task.participant=[]
        this.task.remark=""
        
        this.emptyErrorText()
    },
    saveBtn(){
      if(this.task.content && this.task.startDate && this.task.endDate && this.task.executor){
        this.$emit("saveBtn",this.handleRankType(this.task));
      }
      // 这种写法解决 多个输入框为空时，提示信息不能同时标红
      let isErrorText = []
      if(!this.task.content){
        isErrorText.push(true)
      }else{
        isErrorText.push(false)
      }

      if(!this.task.startDate){
        isErrorText.push(true)
      }else{
        isErrorText.push(false)
      }

      if(!this.task.endDate){
        isErrorText.push(true)
      }else{
        isErrorText.push(false)
      }

      if(this.task.executor.length === 0){
        isErrorText.push(true)
      }else{
        isErrorText.push(false)
      }
      // 统一判断输入框是否为空
      for(let i=0;i<isErrorText.length;i++){
        if(isErrorText[i]){
          this.errorText[i]='输入不能为空'
        }else{
          this.errorText[i]=''
        }
      }
    },
    handleRankType(task){
      for(let i=0;i<this.taskRank.length;i++){
        if(task.rank.rankNum === this.taskRank[i].rankNum){
          task.rank.rankName = this.taskRank[i].rankName
        }
      }
      for(let i=0;i<this.taskType.length;i++){
        if(task.type.typeNum === this.taskType[i].typeNum){
          task.type.typeName= this.taskType[i].typeName
        }
      }
      return task
    },
    emptyErrorText(){
      for(let i=0;i<5;i++){
        this.errorText[i]=''
      }
    }
  },
  created(){
  },
  watch:{
    task:{
      handler(newValue){
        if(newValue.content){
          this.errorText[0]=''
        }
        if(newValue.startDate){
          this.errorText[1]=''
        }
        if(newValue.endDate){
          this.errorText[2]=''
        }
        if(this.task.executor.length != 0){
          this.errorText[3]=''
        }
      },
      deep:true
    }
  }
};
</script>

<style lang="less">

@import "../../common/less/index.less";

.mu-radio{
  margin-right: 8px;
}
.mu-chip{
  background: @theme_color_1;
  color:#fff;
}
.mu-popover{
.mu-secondary-text-color,.mu-option.is-selected .mu-item{
    color: @theme_color_1;
  }
}
</style>



