<template>
    <mu-dialog width="500" scrollable :overlay-close="false" :open.sync="openTextDialog">
        {{dialogMessage}}
        <mu-button slot="actions" flat color="primary" @click="closeBtn()" v-show="isShowCloseBtn">取消</mu-button>
        <mu-button slot="actions" color="primary" @click="confirmBtn()">确定</mu-button>
    </mu-dialog>
</template>

<script type="text/ecmascript-6">

export default {
name: 'DialogText',
  data() {
    return {
      changeEdit:false
    }
  },
  props:{
      openTextDialog:{
          type:Boolean,
          default:false
      },
      dialogMessage:{
          type:String,
          default:'未启用任何内容'
      },
      isShowCloseBtn:{
          type:Boolean,
          default:true
      }
  },
  computed:{
  },
  methods: {
    closeBtn () {
      this.$emit('closeTextDialog');
    },
    confirmBtn(){
      this.$emit("confirmBtn");
    }
  },
  watch:{
  }
}
</script>

<style lang="less">
</style>



