<template>
  <div class="header">
    <mu-appbar color="primary">
      <mu-button icon slot="left" @click="openNav()">
        <mu-icon value="menu"></mu-icon>
      </mu-button>
      企业协作平台
      <mu-button flat slot="right" @click="goBack()">
        <mu-icon value="arrow_back_ios"></mu-icon>
        返回上一页
      </mu-button>
    </mu-appbar>
  </div>
</template>

<script type="text/ecmascript-6">

import { mapGetters, mapMutations } from "vuex";

export default {
name: 'Header',
  data() {
    return {
    }
  },
  components: {
  },
  computed: {
    ...mapGetters([
      'isNavOpen'
    ])
  },
  methods:{
    openNav(){
      this.SET_NAVOPEN(true)
    },
    goBack(){
      this.$router.go(-1)
    },
    ...mapMutations([
      'SET_NAVOPEN'
    ])
  }
}
</script>

<style lang="less">
.mu-appbar-right{
  .mu-icon{
    width: 30px;
  }
}
</style>



