<template>
  <div class="addressCheck">
        <div class="address_box">
            <mu-select class="address_select" label="选择省份" v-model="checkedProvince" :error-text="uAddrErrorText" color="secondary" label-float>
                <mu-option v-for="province,index in provinces" :key="province.code" :label="province.name" :value="province" 
                           v-if="province.level===1"></mu-option>
            </mu-select>
            <mu-select class="address_select" label="选择城市" v-model="checkedCity" :error-text="uAddrErrorText" color="secondary" label-float>
                <mu-option v-for="city,index in provinces" :key="city.code" :label="city.name" :value="city" 
                           v-if="city.sheng===checkedProvince.sheng && city.level===2"></mu-option>
            </mu-select>
            <mu-select class="address_select" label="选择区县" v-model="checkedArea" :error-text="uAddrErrorText" color="secondary" label-float>
                <mu-option v-for="area,index in provinces" :key="area.code" :label="area.name" :value="area" 
                           v-if="area.sheng===checkedProvince.sheng && area.di===checkedCity.di && area.name!=checkedCity.name "></mu-option>
            </mu-select>
        </div>
        <mu-text-field v-model="overAddress" label="街道地址" color="secondary" label-float></mu-text-field><br/>
  </div>
</template>

<script type="text/ecmascript-6">

import * as Address from "../../api/provinces.js"

export default {
name: 'AddressCheck',
  data() {
    return {
      provinces:Address.provinceList,
      userAddress:'',
      overAddress:'',
      checkedProvince:'',
      checkedCity:'',
      checkedArea:'',
      cityIndex:0,
      areaIndex:0,
      provinceId:'',
      cityId:'',
      isUseIdCard:false
    }
  },
  props:{
      idCardAreaId:{
          type:String,
          default:''
      },
      uAddrErrorText:{
          type:String,
          default:''
      }
  },
  methods:{
  },
  created(){
       // 默认显示北京市
    //    this.checkedProvince=this.provinces[0]
    // this.checkedProvince=''
    // this.checkedCity=''
    // this.checkedArea=''
  },
  watch:{
      checkedProvince(newV,oldV){///////////////////////////////////////////////有身份证时无法自动匹配
      
            let index=this.provinces.findIndex(item=>{
                return item.sheng === newV.sheng && item.level===2
            })
            if(this.cityIndex===0){ // 手动设置选择地址
                this.checkedCity=this.provinces[index]
            }else{ // 根据身份证自动匹配地址
                this.checkedCity=this.provinces[this.cityIndex]
                this.cityIndex=0
            }
            // console.log(this.cityIndex)
      },
      checkedCity(newV,oldV){
          let index=this.provinces.findIndex(item=>{
              return item.sheng === this.checkedProvince.sheng && item.di===newV.di
          })
          if(this.areaIndex===0){
            if(this.provinces[index].name===newV.name){
                if(this.provinces[index+1].name === '市辖区'){
                    this.checkedArea=this.provinces[index+2]
                }else{
                    this.checkedArea=this.provinces[index+1]
                }
            }
          }else{
            this.checkedArea=this.provinces[this.areaIndex]
            this.areaIndex=0
          }
          
      },
      checkedArea(newV,oldV){
         this.userAddress=`${this.checkedProvince.name}-${this.checkedCity.name}-${newV.name}  ${this.overAddress}`
         this.$emit('getUserAddress',this.userAddress)
      },
      overAddress(){
         this.userAddress=`${this.checkedProvince.name}-${this.checkedCity.name}-${this.checkedArea.name}  ${this.overAddress}`
         this.$emit('getUserAddress',this.userAddress)
      },
      idCardAreaId(newV){
          //   获取县市区的代号
          this.provinceId=newV.substring(0,2)
          this.cityId=newV.substring(2,4)
          let areaId=newV.substring(4,6)

          // 设置省份
          let provinceIndex = this.provinces.findIndex(item=>{
              return item.sheng === this.provinceId
          })
          this.checkedProvince=this.provinces[provinceIndex]

          // 设置城市
          this.cityIndex = this.provinces.findIndex(item=>{
              return item.sheng === this.provinceId && item.di === this.cityId
          })
          this.checkedCity=this.provinces[this.cityIndex]

          // 设置区县
          this.areaIndex = this.provinces.findIndex(item=>{
              return item.sheng === this.provinceId && item.di === this.cityId && item.xian === areaId
          })
          this.checkedArea=this.provinces[this.areaIndex]
      }
  }
}
</script>

<style lang="less">

@import "../../common/less/index.less";
.addressCheck{
    margin-top: 16px;
    
}
.mu-option.is-selected .mu-item {
    color: @theme_color_2;
}
.address_select{
    width:25%!important;
    margin-right: 2%;
    &:nth-child(3){
        margin-right: 0;
    }
}
</style>



