<template>
  <div class="taskLists" v-cloak>
    <mu-flex  justify-content="center">
      <mu-breadcrumbs divider="—">
    <mu-breadcrumbs-item>{{proList[currentProIndex].name}}</mu-breadcrumbs-item>
  </mu-breadcrumbs>
    </mu-flex>
  
      <!-- <mu-row gutter> -->
        <Draggable v-model="taskLists" class="draggable" :options="{animation: 300,delay:1000,ghostClass:'ghostClass',sort:true}">
                <transition-group>

                    <mu-col span="12" sm="6" md="5" lg="4" xl="3" v-for="(taskList,index) in taskLists" :key="index">
                        <mu-paper :z-depth="1" class="demo-list-wrap">
                            <mu-appbar>
                                <div>{{taskList.name}}</div>
                                <mu-button flat slot="right" @click="addTask(taskList)">
                                    <mu-icon value="add"></mu-icon>
                                    新建任务
                                </mu-button>
                                <mu-menu slot="right" open-on-hover>
                                    <mu-button flat>
                                        <mu-icon value="expand_more"></mu-icon>
                                    </mu-button>
                                    <mu-list slot="content">
                                      <mu-list-item button @click="editList(taskList)">
                                          <mu-icon value="edit" :size="20" color="primary"></mu-icon>&nbsp;
                                          修改列表名称
                                      </mu-list-item>
                                      <!-- <mu-list-item button @click="deleteList()">
                                          <mu-icon value="move_to_inbox" :size="20" class="list_delete_btn"></mu-icon>&nbsp;
                                          移动本列表所有任务到
                                      </mu-list-item> -->
                                      <mu-list-item button @click="deleteList(taskList)">
                                          <mu-icon value="delete" :size="20" color="warning"></mu-icon>&nbsp;
                                          删除列表
                                      </mu-list-item>
                                    </mu-list>
                                </mu-menu>
                            </mu-appbar>
                            <mu-list>

                              

                            <Draggable v-model="taskList.tasks"
                            :options="{animation: 300,draggable:'.done',ghostClass:'ghostClass',sort:true}">
                                <transition-group>
                                    
                                    <mu-list-item avatar button @click="editTask(taskList,task)" :class="handleTypeOrRank('type_color_',task.type.typeNum,'done',task.sorted)" v-for="(task,index) in taskList.tasks" :key="index">
                                        <mu-list-item-action @click.stop="checkTask(taskList,task)">
                                            <mu-checkbox v-model="task.checked" readonly color="primary"></mu-checkbox>
                                        </mu-list-item-action>
                                        <mu-list-item-content>
                                            <mu-list-item-title>{{task.content}}</mu-list-item-title>
                                            <mu-list-item-sub-title>
                                              <span class="task_rank" :class="handleTypeOrRank('rank_color_',task.rank.rankNum)">{{task.rank.rankName}}</span> 
                                              <span class="task_end_date">{{task.endDate}}</span>
                                                
                                            </mu-list-item-sub-title>
                                            
                                        </mu-list-item-content>
                                        <mu-list-item-action>
                                            <mu-avatar>
                                            <img :src="task.executor.avatar">
                                            </mu-avatar>
                                        </mu-list-item-action>
                                        <mu-list-item-action @click.stop="deleteTask(taskList,task)">
                                            <mu-button flat small class="task_delete_btn">
                                                <mu-icon value="delete" color="primary"></mu-icon>
                                            </mu-button>
                                        </mu-list-item-action>
                                    </mu-list-item>

                                </transition-group>
                            </Draggable>
                            
                            </mu-list>
                      </mu-paper>
                    </mu-col>

                </transition-group>
        </Draggable>
        <!-- <mu-col span="12" sm="6" md="5" lg="4" xl="3" v-for="(taskList,index) in taskLists" :key="index">
            <mu-paper :z-depth="1" class="demo-list-wrap">
                <mu-appbar>
                    <div>{{taskList.name}}</div>
                    <mu-button flat slot="right" @click="addTask(taskList)">
                        <mu-icon value="add"></mu-icon>
                        新建任务
                    </mu-button>
                    <mu-menu slot="right" open-on-hover>
                        <mu-button flat>
                            <mu-icon value="expand_more"></mu-icon>
                        </mu-button>
                        <mu-list slot="content">
                          <mu-list-item button @click="editList(taskList)">
                              <mu-icon value="edit" :size="20" color="primary"></mu-icon>&nbsp;
                              修改列表名称
                          </mu-list-item>
                          <mu-list-item button @click="deleteList(taskList)">
                              <mu-icon value="delete" :size="20" color="warning"></mu-icon>&nbsp;
                              删除列表
                          </mu-list-item>
                        </mu-list>
                    </mu-menu>
                </mu-appbar>
                <mu-list>
                    <mu-list-item avatar button @click="editTask(taskList,task)" :class="handleTypeOrRank('type_color_',task.type.typeNum)" v-for="(task,index) in taskList.tasks" :key="index">
                        <mu-list-item-action @click.stop="checkTask(taskList,task)">
                            <mu-checkbox v-model="task.checked" readonly color="primary"></mu-checkbox>
                        </mu-list-item-action>
                        <mu-list-item-content>
                            <mu-list-item-title>{{task.content}}</mu-list-item-title>
                            <mu-list-item-sub-title>
                              <span class="task_rank" :class="handleTypeOrRank('rank_color_',task.rank.rankNum)">{{task.rank.rankName}}</span> 
                              <span class="task_end_date">{{task.endDate}}</span>
                            </mu-list-item-sub-title>
                        </mu-list-item-content>
                        <mu-list-item-action>
                            <mu-avatar>
                            <img :src="task.executor.avatar">
                            </mu-avatar>
                        </mu-list-item-action>
                        <mu-list-item-action @click.stop="deleteTask(taskList,task)">
                            <mu-button flat small class="task_delete_btn">
                                <mu-icon value="delete" color="primary"></mu-icon>
                            </mu-button>
                        </mu-list-item-action>
                    </mu-list-item>
                </mu-list>
          </mu-paper>
            
        </mu-col> -->
      <!-- </mu-row> -->
    
      <!-- 任务列表页  "修改任务列表"弹出框 -->

      <DialogInput 
      :dialogTit="'修改任务列表'" :isOpenDialog="openEditListDialog"
      @closeDialog="closeDialog" @resetBtn="editListResetBtn" @saveBtn="editListSaveBtn">
        <div slot="content">
           <form action="">
              <mu-text-field v-model="taskListName" label="任务列表名称" @blur="listNameBlur()" color="primary" label-float full-width></mu-text-field><br/>
           </form>
        </div>
      </DialogInput>
      
      <!-- 点击 “新建任务” 或 “编辑任务” 弹出框 -->
      <HandleTask 
      :dialogTit="taskTit" :isOpenDialog="openTask" :isEdit="isEditAddTask" :isDisabled="isDisabled" :task="task" :members="members"
      @closeDialog="closeDialog" @editBtn="taskEditBtn" @saveBtn="taskSaveBtn"></HandleTask>

      <!-- 点击 “删除列表” 弹出框 -->

      <DialogText 
      :openTextDialog="openListDeleteDialog" :dialogMessage="'确定删除这个任务列表吗?'"
      @closeTextDialog="closeDelDialog" @confirmBtn="listConfirmBtn"></DialogText>

      <!-- 点击 “删除” 任务 弹出框 -->

      <DialogText 
      :openTextDialog="taskDelete" :dialogMessage="'确定删除这条任务吗？'"
      @closeTextDialog="closeDelDialog" @confirmBtn="taskConfirmBtn"></DialogText>
  </div>
</template>

<script type="text/ecmascript-6">

import Vue from "vue";
import HandleTask from '../handleTask/HandleTask.vue'
import DialogInput from "../dialog/DialogInput.vue";
import DialogText from "../dialog/DialogText.vue";
import Draggable from 'vuedraggable'

import { mapGetters, mapMutations , mapActions } from "vuex";

// 自定义时间戳过滤器

Vue.filter("reverse", (list) => {
    return list.reverse()
});

export default {
  name: "TaskLists",
  data() {
    return {
      taskLists:this.$route.params.taskLists,
      members:this.$route.params.members,
      //  弹出框相关
      openListDeleteDialog:false,
      taskDelete:false,
      openEditListDialog:false,
      taskListName:'',
      openTask:false,
      isEditAddTask:false, // 控制“编辑”按钮
      isEditTask:false, // 判断弹出框是不是“编辑任务”的弹出框
      isDisabled:false,
      taskTit:"新建任务",
      task: {
          id:-1,
          content: "",
          rank:
            {
              rankNum:0,
              rankName:"普通"
            },
          startDate: "",
          endDate:"",
          executor:[],
          participant:[],
          remark:"",
          checked:false,
          type:{
            typeNum:0,
            typeName:"待办"
          }
      },
      currentProIndex:-1,
      clickIndex:-1,
      clickTaskIndex:-1,
      listNameErrorText:''
    };
  },
  components: {
      DialogInput,
      DialogText,
      HandleTask,
      Draggable
  },
  computed:{
    ...mapGetters([
      'proList',
      'currentProId'
    ])
  },
  methods: {
    // 控制任务左侧边框颜色和时间背景颜色
    handleTypeOrRank(classPre,num,doneClass,sorted){
      if(classPre === 'type_color_'){
        
        if(doneClass && sorted){
          // console.log('已完成：'+classPre+num)
          return classPre+num
        }else{
          // console.log(`${classPre}${num} ${doneClass}`)
          return `${classPre}${num} ${doneClass}`
        }

      }else if(classPre === 'rank_color_'){
        // console.log('rank的class处理')
        return classPre+num
      }
      
      // console.log(doneClass,sorted)
      
    },
    checkTask(taskList,task) {
      if(task.type.typeNum>2){
        return
      }
      task.checked = !task.checked;
      task.type.typeNum++
      switch (task.type.typeNum) {
        case 0:
          task.type.typeName='待办'
          break
        case 1:
          task.type.typeName='进行中'
          break
        case 2:
          task.type.typeName='待审核'
          break
        default:3
          task.type.typeName='已完成'
          break
      }
      setTimeout(() => {
        task.checked = false;
        if(task.type.typeNum === 3){
          this.clickIndex=this.getClickIndex(this.taskLists,taskList.id)
          this.handleTaskList(this.clickIndex)
        }
        
      }, 500)
    },
    // --------------------------------------------输入弹出框

    // “修改任务列表” 弹出框相关
    editList(taskList){ 
      this.openEditListDialog=true
      this.clickIndex=this.getClickIndex(this.taskLists,taskList.id)
      this.taskListName=taskList.name
    },
    editListResetBtn(){
      this.taskListName=''
    },
    editListSaveBtn(){
      let proList=this.proList
      // let taskLists = proList[this.currentProIndex].taskLists
      let taskList = this.taskLists[this.clickIndex]
      taskList.name=this.taskListName
      this.upDateProList(proList)
      this.openEditListDialog=false
    },

    // 列表名称不能为空
    listNameBlur(){
      if(!this.taskListName){
        this.listNameErrorText='任务列表名称不能为空'
        return
      }else{
        this.listNameErrorText=''
      }
    },
    // “添加任务” 弹出框相关
    addTask(taskList){
      this.openTask=true 
      this.taskTit="新建任务"
      this.isEditAddTask=false
      this.isDisabled=false
      this.task = {
          id:-1,
          content: "",
          rank:
            {
              rankNum:0,
              rankName:"普通"
            },
          type:
            {
              typeNum:0,
              typeName:"待办"
            },
          startDate: "",
          endDate:"",
          executor:[],
          participant:[],
          remark:"",
          checked:false
      } 

      this.clickIndex=this.getClickIndex(this.taskLists,taskList.id)
    },
    // “编辑任务” 弹出框相关
    editTask(taskList,task){
      this.openTask=true 
      this.taskTit="查看任务"
      this.isEditAddTask=true
      this.isEditTask=true
      this.isDisabled=true
      this.task = {
          id: task.id,
          content: task.content,
          rank:Object.assign({}, task.rank),
          type:Object.assign({}, task.type),
          startDate: task.startDate,
          endDate:task.endDate,
          executor:task.executor,
          participant:task.participant,
          remark:task.remark,
          checked:false
      } 
      this.clickIndex=this.getClickIndex(this.taskLists,taskList.id)
      this.clickTaskIndex=this.getClickIndex(taskList.tasks,task.id)
    },
    taskEditBtn(){
      this.taskTit="编辑任务"
      this.isEditAddTask=false
      this.isDisabled=false
    },
    // 公用保存按钮
    taskSaveBtn(task){
      
      // let taskList=this.proList[this.currentProIndex].taskLists[this.clickIndex]
      let taskList=this.taskLists[this.clickIndex]
      // let newTask=Object.assign({}, task)
      let newTask = task
      newTask.startDate=this.handleDate(newTask.startDate)
      newTask.endDate=this.handleDate(newTask.endDate)

      if(this.isEditTask === false){ // 添加任务
        newTask.id=taskList.tasks.length
        taskList.tasks.unshift(newTask)
      }else{ // 编辑任务
        taskList.tasks[this.clickTaskIndex]=newTask
        this.handleTaskList(this.clickIndex)
      }
      this.isEditTask=false 
      this.openTask=false  
    },
    // 公用关闭弹出框开关
    closeDialog(){
      this.openEditListDialog=false
      this.openTask=false  
    },
    // -------------------------------------------------文本弹出框
    
    // 删除任务列表 弹出框相关
    deleteList(taskList){ 
      this.clickIndex=this.getClickIndex(this.taskLists,taskList.id)
      this.openListDeleteDialog=true
    },
    listConfirmBtn(){
      let proList=this.proList
      // let taskLists = proList[this.currentProIndex].taskLists
      let taskLists=this.taskLists
      taskLists.splice(this.clickIndex,1)
      this.openListDeleteDialog=false
    },
    
    // 删除任务 弹出框相关
    deleteTask(taskList,task){
      this.taskDelete=true
      this.clickIndex=this.getClickIndex(this.taskLists,taskList.id)
      this.clickTaskIndex=this.getClickIndex(taskList.tasks,task.id)
    },
    taskConfirmBtn(){ 
      // let taskList=this.proList[this.currentProIndex].taskLists[this.clickIndex]
      let taskList=this.taskLists[this.clickIndex]
      taskList.tasks.splice(this.clickTaskIndex,1)
      this.taskDelete=false
    },
    // 公用关闭弹出框开关
    closeDelDialog(){
      this.openListDeleteDialog=false
      this.taskDelete=false
    },
    handleDate(oDate){
      // muse-ui的date插件生成时间的是date对象，已有数据是“xxxx-xx-xx”的字符串，所以需要区分处理一下
      if(oDate.length!=10){
        let month = oDate.getMonth()<10 ? `0${oDate.getMonth()+1}`: oDate.getMonth()+1
        let day = oDate.getDate()<10 ? `0${oDate.getDate()}`: oDate.getDate()
        let nDate = `${oDate.getFullYear()}-${month}-${day}`
        return nDate;
      }else{
        return oDate
      }
    },
    getProIndex(){
      this.currentProIndex=this.getClickIndex(this.proList,this.currentProId)
    },
    // 公用获取被点击的索引方法
    getClickIndex(list,id){
      let index=list.findIndex((item)=>{
        return item.id === id
      })
      return index
    },
    // 任务列表排序 ，已完成的任务排到最后
    handleAllTaskLists(){
      for(let i=0;i<this.taskLists.length;i++){
        this.handleTaskList(i)
      }
    },
    handleTaskList(i){////////////////////////////////////////有问题
      for(let j=0;j<this.taskLists[i].tasks.length;j++){
          if(this.taskLists[i].tasks[j].type.typeNum === 3 && !this.taskLists[i].tasks[j].sorted){
            this.taskLists[i].tasks[j].sorted = true
            let index = this.getClickIndex(this.taskLists[i].tasks,this.taskLists[i].tasks[j].id)
            this.taskLists[i].tasks.push(this.taskLists[i].tasks[j])
            this.taskLists[i].tasks.splice(index,1)
          }
      }
    },
    ...mapMutations([
      'SET_CURRENTPROID'
    ]),
    ...mapActions([
      'upDateProList'
    ])
  },
  created(){
    // 设置进入的当前项目的id
    this.SET_CURRENTPROID(this.$route.params.id)
    this.getProIndex()
    this.handleAllTaskLists()
  },
  watch:{
  }
};
</script>

<style lang="less">
@import "../../common/less/index.less";

.taskLists {
  .row,.draggable {
    padding: 16px;
  }
  .mu-paper {
    background: #fff;
    margin-bottom: 16px;
    transition: all 0.5s;
    box-shadow: 0 2px 1px -1px rgba(0, 0, 0, 0.2),
      0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 3px 0 rgba(0, 0, 0, 0.12);

    &:hover {
      box-shadow: 0 0 15px 5px rgba(0, 0, 0, 0.06);
    }
  }
  .mu-appbar {
    box-shadow: none;
  }
  .mu-list {
    height: 720px;
    overflow-y: auto;
  }
  .mu-item {
    padding: 40px 16px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  }
  .mu-appbar-title{
    div{
      .list_nowrap
    }
  }
  .task_delete_btn{
    min-width: 50px;
    margin-left: 10px;

    &::before{
      background: none;
    }
  }
}

.list_delete_btn{
  color:@theme_color_2!important;
}
.type_color_0{
  border-left:3px solid rgb(235, 211, 0);
}
.type_color_1{
  border-left:3px solid @theme_color_2;
}
.type_color_2{
  border-left:3px solid @theme_color_1;
}
.type_color_3{
  border-left:3px solid #ccc;
  .task_end_date{
    border:1px solid rgb(143, 143, 143);
  }
  .task_rank{
    background: #ccc!important;
  }
  .mu-avatar{
    opacity: 0.3;
  }
  i{
    color:#ccc;
  }
}
.task_end_date{
  // color:@theme_color_1;
    border-radius: 3px;
    font-size: 0.8em;
    margin-top: 3px;
    padding: 0 5px;
    border:1px solid @theme_color_1;
}
.task_rank{
    display: inline-block;
    padding: 0 5px;
    margin-right:5px;
    color: #fff;
    width: auto;
    border-radius: 3px;
    font-size: 0.8em;
    margin-top: 3px;

    &:nth-child(3){
      background:rgba(0, 0, 0, 0.38);
    }
}
.rank_color_0{
  background: @theme_color_1!important;
}
.rank_color_1{
  background: @theme_color_2!important;
}
.rank_color_2{
  background: rgb(194, 57, 23)!important;
}
// 拖动相关
// .ghostClass{
//   opacity: 0.15;
// }
.mu-list{
  &>div>span>li{
    display: block;
  }
}
.mu-breadcrumbs {
  margin-bottom: -16px;
  margin-right: 8px;
  font-size: 16px;
  a{
    color:@theme_color_1!important;
  }
}
</style>



