<template>
  <div class="projectList" v-cloak>
    <!-- <mu-row gutter> -->
      
    <Draggable v-model="proList" class="draggable" :options="{animation: 300,delay:1000,ghostClass:'ghostClass',chosenClass:'dragClass'}">
            <transition-group>

                <mu-col span="12" sm="6" md="5" lg="4" xl="3" v-for="(project,index) in proList" :key="index">
                  <mu-card @click="toTaskLists(project)">
                    <mu-card-media>
                      <img :src="project.coverImg" ref="coverImg">
                    </mu-card-media>
                    <mu-card-title :title="project.name" :sub-title="project.describe" class="project_describe"></mu-card-title>
                    <mu-card-actions>
                      <mu-button flat @click.stop="edit(project,index)"><mu-icon :size="20" value="edit" color="primary"></mu-icon> 编辑</mu-button>
                      <mu-button flat @click.stop="invite(index)"><mu-icon :size="20" value="perm_identity" color="secondary"></mu-icon> 邀请</mu-button>
                      <mu-button flat @click.stop="deleteProject(index)"><mu-icon :size="20" value="delete" color="warning"></mu-icon> 删除</mu-button>
                    </mu-card-actions>
                  </mu-card>
                </mu-col>

            </transition-group>
    </Draggable>

    <!-- </mu-row> -->
    
    <!-- <Draggable v-model="test" @update="datadragEnd":options="{animation: 300,ghostClass:'ghostClass',chosenClass:'dragClass'}" style="overflow:hidden;margin-bottom:50px;">
            <transition-group>

                <Draggable v-model="tags.data" @update="datadragEnd" v-for="(tags,index) in test" :key="index" @end="checkMove(tags)" :move="startDrop" :options="{animation: 300,group:'test',dragClass:'dragClass',ghostClass:'ghostClass',draggable:'.dragClass',sort:true,disabled:tags.disabled}" style="float:left;margin-left:50px;padding:30px;background:#ccc;min-height:100px;min-width:200px;">
                    <transition-group>
                        <div v-for="element in tags.data" :key="element.name" :class="{'dragClass':element.id!=8}" style="padding:5px 10px;width:150px;text-align:center;border:1px solid red;">
                          {{element.name}}
                        </div>
                    </transition-group>
                </Draggable>

            </transition-group>
    </Draggable> -->

      <!-- 点击 “编辑” 弹出框 -->
      <HandleProject 
      :dialogTit="'编辑项目'" :isOpenDialog="openEditProDialog" :content="content" 
      @closeDialog="closeDialog"  @saveBtn="editProSaveBtn" @chooseCoverImg="chooseCoverImg"
      ref="handleProject"></HandleProject>

      <!-- 点击 “邀请” 弹出框 -->
      <HandleInvite 
      :openInviteDialog="openInviteDialog"
      @closeDialog="closeDialog" @saveBtn="inviteSaveBtn"></HandleInvite>

      <!-- 点击 “删除” 弹出框 -->
      <DialogText 
      :openTextDialog="openDelProDialog" :dialogMessage="'确定删除本项目吗？'"
      @closeTextDialog="closeDialog" @confirmBtn="delProConfirmBtn"></DialogText>

      <!-- 邀请人已存在项目中 弹出框 -->
      <DialogText 
      :openTextDialog="openHaveInvDialog" :dialogMessage="`项目成员中已有  ${haveInvMembers}，不需添加`" :isShowCloseBtn="false"
      @confirmBtn="closeHaveInvDialog"></DialogText>

      <!-- 邀请人添加成功 弹出框 -->
      <DialogText 
      :openTextDialog="openSucInvDialog" :dialogMessage="`成功邀请 ${invSucMembers} ！`" :isShowCloseBtn="false"
      @confirmBtn="closeSucInvDialog"></DialogText>
  </div>
</template>

<script type="text/ecmascript-6">

import HandleProject from '../handleProject/HandleProject.vue'
import HandleInvite from '../handleInvite/HandleInvite.vue'
import DialogText from "../dialog/DialogText.vue"
import Draggable from 'vuedraggable'

import { mapGetters, mapMutations , mapActions } from "vuex";

export default {
  name: "ProjectList",
  data() {
    return {
      test:[
        {
          'flag':1,
          'disabled':false,
          'data':[{id:0,name:'11'},{id:1,name:'22'},{id:3,name:'33'},{id:4,name:'44'}]
        },{
          'flag':2,
          'disabled':false,
          'data':[{id:5,name:'55'},{id:6,name:'66'},{id:7,name:'77'},{id:8,name:'88'}]
        }
      ],
      tags:[{id:0,name:'11'},{id:1,name:'22'},{id:3,name:'33'},{id:4,name:'44'}],
      tags2:[{id:5,name:'55'},{id:6,name:'66'},{id:7,name:'77'},{id:8,name:'88'}],
      
      openEditProDialog:false,
      openInviteDialog:false,
      openHaveInvDialog:false,
      openSucInvDialog:false,
      haveInvMembers:[],
      invSucMembers:[],
      content:{
            projrctName:'',
            projrctDescribe:'',
            coverImgSrc:''
      },
      openDelProDialog:false,
      editProIndex:-1,
      delProIndex:-1,
      invProIndex:-1
    };
  },
  components: {
    HandleProject,
    HandleInvite,
    DialogText,
    Draggable
  },
  computed:{
    proList: {
        get() {
            return this.$store.state.proList
        },
        set(list) {
            this.$store.commit('SET_PROLIST', list)
        }
    }
  },
  methods:{
    toTaskLists(project){
      this.$router.push({name:'TaskLists',params:project})
    },
    // “编辑” 按钮相关
    edit(project,index){
      this.editProIndex=index
      this.openEditProDialog=true
      this.content={
            projrctId:project.id,
            projrctName : project.name,
            projrctDescribe: project.describe,
            coverImgSrc: project.coverImg
      }
    },
    editProSaveBtn(content){
      let projectList=this.proList
      projectList[this.editProIndex].name=content.projrctName
      projectList[this.editProIndex].describe=content.projrctDescribe
      projectList[this.editProIndex].coverImg=content.coverImgSrc
      this.upDateProList(projectList)
      
      this.openEditProDialog=false
    },
    // “邀请” 按钮相关
    invite(index){
      this.invProIndex=index
      this.haveInvMembers=[]
      this.invSucMembers=[]
      this.openInviteDialog=true
    },
    inviteSaveBtn(checkedMembers){
      let projectList = this.proList
      let members = projectList[this.invProIndex].members

      for(let i=0;i<checkedMembers.length;i++){
        
        let index = members.findIndex((item)=>{
          return item.nid === checkedMembers[i].id
        })

        if(index === -1){
          // 替换属性id为nid,与project的menmber属性保持一致
          checkedMembers[i].nid = checkedMembers[i].id
          delete checkedMembers[i].id

          projectList[this.invProIndex].members.push(checkedMembers[i])
          this.invSucMembers.push(checkedMembers[i].name)
        }else{
          this.haveInvMembers.push(checkedMembers[i].name)
        }
        
      }
      if(this.haveInvMembers.length > 0 && this.invSucMembers.length > 0){
        this.openHaveInvDialog=true
        this.openSucInvDialog=true
      }else if(this.haveInvMembers.length > 0){
        this.openHaveInvDialog=true
      }else if(this.invSucMembers.length > 0){
        this.openSucInvDialog=true
      }

      this.openInviteDialog=false
    },
    // 关闭“邀请结果”弹出框
    closeHaveInvDialog(){
        this.openHaveInvDialog=false
    },
    closeSucInvDialog(){
        this.openSucInvDialog=false
    },
    // “删除” 按钮相关
    deleteProject(index){
      this.delProIndex=index
      this.openDelProDialog=true
    },
    delProConfirmBtn(){
      let projectList=this.proList
      projectList.splice(this.delProIndex,1)
      this.upDateProList(projectList)

      this.openDelProDialog=false
    },
    // 三弹出框公用 关闭开关
    closeDialog(){
      this.openEditProDialog=false
      this.openInviteDialog=false
      this.openDelProDialog=false
      this.openHaveInvDialog=false
      this.openSucInvDialog=false
    },
    chooseCoverImg(coverImg){
      this.content.coverImgSrc=coverImg
    },
    ...mapActions([
      'upDateProList'
    ])
  },
  mounted(){
    
      // console.log(this.proList)
  }
};
</script>

<style lang="less">
.projectList{
  .row,.draggable{
    padding: 16px;
  }
  .mu-card{
    transition: all 0.5s;
    margin-bottom: 16px;
    cursor: pointer;
    &:Hover{
      box-shadow:0 0 15px 5px rgba(0,0,0,0.06);
    }
  }
}
.draggable{
  &>span{
    display: flex;
    flex-wrap: wrap;
    margin-right: -8px;
    margin-left: -8px;
    flex-direction: row!important;
  }
}
.project_describe{
  .mu-card-sub-title{
    height: 18px;
  }
}
</style>



