import * as types from './mutationTypes'
import state from './state'

const mutations = {
    [types.SET_NAVOPEN](state, isOpen) {
        state.isNavOpen = isOpen
    },
    [types.SET_PROLIST](state, proList) {
        state.proList = proList
    },
    [types.SET_USERS](state, users) {
        state.users = users
    },
    [types.SET_CURRENTPROID](state, id) {
        state.currentProId = id
    }
}
export default mutations


