import * as ApiPath from "../api/apiPath.js"
const Mock = require("mockjs")
    // const Random = Mock.Random


const projectName = [
    '企业协作管理平台',
    '外买APP',
    '城市规划平台',
    '交通管理系统',
    '某公司考勤系统',
    'xx程序学习平台'
]

export default Mock.mock(ApiPath.getProject, "get", {
    "code": 0,
    "data": {
        'projectList|1-6': [{
            'id|+1': 0,
            'name|+1': projectName,
            'describe': '@ctitle(5,15)',
            'coverImg|1': ApiPath.coverImg,
            'members|1-10': [{
                'nid': '@increment',
                'name': '@cname',
                'email': '@email(163.com)',
                'IDcard': '@id',
                'role|1': [
                    { 'roleNum': 0, 'roleName': 'admin' },
                    { 'roleNum': 1, 'roleName': 'staff' }
                ],
                'avatar|1': ApiPath.avatar
            }],
            'taskLists|1-6': [{
                'id|+1': 0,
                'name': '@ctitle',
                'tasks|1-5': [{
                    'id|+1': 0,
                    'content': '@cparagraph(1,3)',
                    'startDate': "@date('2018-MM-dd')",
                    'endDate': "@date('2018-MM-dd')",
                    'remark': '@cparagraph(1,2)',
                    'checked': false,
                    'type|1': [
                        { 'typeNum': 0, 'typeName': '待办' },
                        { 'typeNum': 1, 'typeName': '进行中' },
                        { 'typeNum': 2, 'typeName': '待审核' },
                        { 'typeNum': 3, 'typeName': '已完成' }
                    ],
                    'rank|1': [
                        { 'rankNum': 0, 'rankName': '普通' },
                        { 'rankNum': 1, 'rankName': '重要' },
                        { 'rankNum': 2, 'rankName': '紧急' }
                    ],
                    'executor': {
                        'id': '@increment()',
                        'name': '@cname',
                        'avatar|1': ApiPath.avatar
                    },
                    'participant|2-4': [{
                        'id': '@increment()',
                        'name': '@cname'
                    }]
                }]
            }]
        }]
    },
    "msg": "success"
})


