<template>
  <div class="handleInvite">
    <DialogInput 
      :dialogTit="dialogTit" :isOpenDialog="openInviteDialog" :isEdit="isEdit" 
      @closeDialog="closeDialog" @resetBtn="resetBtn" @saveBtn="saveBtn">
        <div slot="content">
           <form action="" class="invite_body">
             <mu-select label="邀请成员" color="primary" v-model="user" multiple filterable :tags="true" chips :label-float="true" full-width>
                <mu-option v-for="theUser,index in users" :key="index" :label="theUser.name" :value="theUser" color="primary"></mu-option>
              </mu-select>
           </form>
        </div>
      </DialogInput>
  </div>
</template>

<script type="text/ecmascript-6">

import DialogInput from "../dialog/DialogInput.vue";

import { mapGetters, mapMutations } from "vuex";

export default {
name: 'HandleInvite',
  data() {
    return {
      dialogTit:"邀请成员",
      isEdit:false,
      user:[],
    }
  },props: {
    openInviteDialog: {
      type: Boolean,
      default: false
    }
  },
  components: {
    DialogInput
  },
  computed:{
    ...mapGetters([
      'users'
    ])
  },
  methods:{
    closeDialog(){
      this.$emit("closeDialog");
      this.user=[]
    },
    resetBtn(){
      this.user=[]
    },
    saveBtn(){
      this.$emit("saveBtn",this.user);
      this.user=[]
    }
  }
}
</script>

<style lang="less">

@import "../../common/less/index.less";

.mu-chip{
  background: @theme_color_1;
  color:#fff;
}
.handleInvite .mu-popover{
.mu-secondary-text-color,.mu-option.is-selected .mu-item{
    color: @theme_color_1;
  }
}
</style>



