<template>
  <div id="app" :class="{'active':isNavOpen}" v-cloak>
    
      <keep-alive>
        <router-view/>
      </keep-alive>
  </div>
</template>

<script>
import DialogInput from './components/dialog/DialogInput.vue'

import { mapGetters, mapMutations } from "vuex";
import "./common/less/animate.min.css";

export default {
  name: "App",
  data() {
    return {
    }
  },
  components:{
  },
  computed: {
    ...mapGetters([
      'isNavOpen'
    ])
  }
};
</script>

<style lang="less">
/* @import '//fonts.useso.com/css?family=Roboto:300,400,500,700,400italic'; */
@import 'http://cdn.bootcss.com/material-design-icons/3.0.1/iconfont/material-icons.css';
#app{
  &.active{
    background: #ccc;
    height: 100vh;
    .app_body{
      min-height: 90vh;
    }
  }
}
</style>



