<template>
  
    <div class="addBtn">
      <mu-button fab color="secondary" @click="add()">
          <mu-icon value="add"></mu-icon>
      </mu-button>

      <!-- 项目列表页  添加项目弹出框 -->

      <HandleProject 
      :dialogTit="'创建项目'" :isOpenDialog="openAddProDialog" :isEdit="false" :content="content" 
      @closeDialog="closeAddDialog" @saveBtn="addProSaveBtn" @chooseCoverImg="chooseCoverImg"></HandleProject>

      
      <!-- 任务列表页  添加任务列表弹出框 -->

      <DialogInput 
      :dialogTit="'创建任务列表'" :isOpenDialog="openAddListDialog" 
      @closeDialog="closeAddDialog" @resetBtn="addListResetBtn" @saveBtn="addListSaveBtn">
        <div slot="content">
           <form action="">
              <mu-text-field v-model="taskListName" label="任务列表名称" :error-text="listNameErrorText" @blur="listNameBlur()" color="primary" label-float full-width></mu-text-field><br/>
           </form>
        </div>
      </DialogInput>


    </div>
</template>

<script type="text/ecmascript-6">

import HandleProject from '../handleProject/HandleProject.vue'
import DialogInput from "../dialog/DialogInput.vue";

import * as ApiPath from "../../api/apiPath.js"
import { mapGetters, mapMutations , mapActions } from "vuex";

export default {
name: 'AddBtn',
  data() {
    return {
      openAddProDialog:false,
      openAddListDialog:false,
      taskListName:'',
      content:{
            projectId:0,
            projrctName:'',
            projrctDescribe:'',
            coverImgSrc: ApiPath.coverImg[0]
      },
      listNameErrorText:''
    }
  },
  components: {
    HandleProject,
    DialogInput
  },
  computed:{
    ...mapGetters([
      'proList',
      'currentProId'
    ])
  },
  methods:{
    add(){
      let routeUrl = this.$route.path
      // 添加项目 弹出框--打开按钮
      if(routeUrl === "/main"){
        this.openAddProDialog=true

      }else if(routeUrl === "/taskLists"){
      // 添加任务列表 弹出框--打开按钮
        this.openAddListDialog=true
      }
    },
    closeAddDialog(){ // 添加项目 和 添加任务列表--保存按钮
      this.openAddProDialog=false
      this.openAddListDialog=false
      this.taskListName=''
      this.listNameErrorText=''
    },
    addProSaveBtn(){ // 添加项目--保存按钮
    
      let id=this.proList.length > 0 ? this.proList[this.proList.length-1].id+1 : 0
      let project={
        'id':id,
        'name':this.content.projrctName,
        'describe':this.content.projrctDescribe,
        'coverImg':this.content.coverImgSrc,
        'members': [],
        'taskLists': []
      }
      let projectList=this.proList
      console.log(project.id)
      projectList.push(project)
      this.upDateProList(projectList)
      
      this.openAddProDialog=false
    },
    chooseCoverImg(coverImg){
      this.content.coverImgSrc=coverImg
    },
    addListResetBtn(){ // 添加任务列表--重置按钮
      this.taskListName=''
      this.listNameErrorText=''
    },
    addListSaveBtn(){// 添加任务列表--保存按钮
      if(!this.taskListName){
        this.listNameErrorText='任务列表名称不能为空'
        return
      }else{
        this.listNameErrorText=''
        // 外层为置空判断，下面为主要业务逻辑
        let index=this.getFindIndex(this.proList,this.currentProId)
        let taskLists=this.proList[index].taskLists
        let id=taskLists.length > 0 ? taskLists[taskLists.length-1].id+1 : 0
        let taskList={
          'id': id,//////////////////////////////////////////////////////////////////////////////////////////////[taskLists.length-1].id+1,
          'name': this.taskListName,
          'tasks': [],
        }
        // console.log(taskLists)
        taskLists.push(taskList)
        this.upDateProList(this.proList)

        this.taskListName=''
        this.openAddListDialog=false
      }
    },
    // 列表名称不能为空
    listNameBlur(){
      if(!this.taskListName){
        this.listNameErrorText='任务列表名称不能为空'
        return
      }else{
        this.listNameErrorText=''
      }
    },
    // 公用获取被点击的索引方法
    getFindIndex(list,id){
      let index=list.findIndex((item)=>{
        return item.id === id
      })
      return index
    },
    ...mapActions([
      'upDateProList'
    ])
  },
  created(){
  }
}
</script>

<style lang="less">

@import "../../common/less/index.less";

.addBtn{
    position: fixed;
    bottom:40px;
    right:40px;
    z-index: 10;
    transition: all 0.5s;
    .mu-fab-button{
            box-shadow: 0 3px 5px -1px rgba(255, 195, 154, 0.4), 0 6px 10px 0 rgba(255, 195, 154, 0.8), 0 1px 18px 0 rgba(255, 195, 154, 0.4);
    }
}
.cover_img{
  margin-top: 20px;
}
.cover_img_tit{
  color:@theme_color_1;
}
.checked_cover_img{
  width:50%;
  margin: 20px auto;

  img{
    .img(100%);
  }
}
.cover_img_box{

  img{
    .img(100%);
    margin: 8px 0;
  }
}
</style>



