<template>
  
    <mu-dialog width="500" scrollable :overlay-close="false" :open.sync="isOpenDialog">
        <div slot="title">{{dialogTit}}</div>
        <slot name="content"></slot>
        <mu-button slot="actions" flat color="primary" @click="closeBtn()">取消</mu-button>
        <mu-button slot="actions" flat color="primary" @click="editBtn()" v-if="isEdit">编辑</mu-button>
        <mu-button slot="actions" flat color="primary" @click="resetBtn()" v-if="!isEdit">重置</mu-button>
        <mu-button slot="actions" color="primary" @click="saveBtn()" v-if="!isEdit">保存</mu-button>
    </mu-dialog>
</template>

<script type="text/ecmascript-6">

export default {
name: 'DialogInput',
  data() {
    return {
      changeEdit:false
    }
  },
  props:{
      isOpenDialog:{
          type:Boolean,
          default:false
      },
      dialogTit:{
        type:String
      },
      dialogFlag:{
          type:String,
          default:'旗帜'
      },
      isEdit:{
          type:Boolean,
          default:false
      }
  },
  computed:{
  },
  methods: {
    closeBtn () {
      this.$emit('closeDialog');
    },
    editBtn(){
      this.$emit('editBtn');
    },
    resetBtn(){
      this.$emit('resetBtn');
    },
    saveBtn(){
      this.$emit("saveBtn");
    }
  },
  watch:{
  }
}
</script>

<style lang="less">
</style>



