<template>
  <div class="login enter">
      <div class="enter_bg animated fadeInDown"></div>
    <mu-card class="enter_card animated fadeInUp">
        <mu-card-media title="登录" sub-title="LOGIN">
            <img src="../../assets/images/login_img.jpg">
        </mu-card-media>
        <mu-card-text>
            <mu-text-field v-model="userEmail" label="账号（邮箱）" :error-text="uEmailErrorText" color="primary" label-float ></mu-text-field><br/>
            <mu-text-field v-model="userPassword" label="密码" :error-text="uPswErrorText"
            :action-icon="visibility ? 'visibility_off' : 'visibility'" :action-click="() => (visibility = !visibility)" 
            :type="visibility ? 'password' : 'text'"  color="primary" label-float></mu-text-field><br/>
        </mu-card-text>
        <mu-card-actions>
            <mu-button large  color="primary" @click="login()">登录</mu-button><br/>
            <div class="login_btns">
                <mu-button flat @click="toRegister()">
                    <mu-icon :size="20" value="perm_identity"></mu-icon>
                     没有账号
                </mu-button>
                <!-- <mu-button flat>
                    <mu-icon :size="20" value="lock_open"></mu-icon>
                     忘记密码
                </mu-button> -->
            </div>
        </mu-card-actions>
    </mu-card>
  </div>
</template>

<script type="text/ecmascript-6">

import mockProject from "../../mock/mockProject.js"
import mockUsers from "../../mock/mockUsers.js"
import { getProject } from "../../api/project.js"
import { getUsers } from "../../api/users.js"

import { mapGetters, mapMutations } from "vuex";

export default {
  name: "Login",
  data() {
    return {
      userEmail: "",
      userPassword: "",
      visibility: true,
      uEmailErrorText:'',
      uPswErrorText:''
    };
  },
  components: {},
  computed:{
    ...mapGetters([
      'proList'
    ])
  },
  methods:{
      login(){
          if(this.userEmail.trim()&&this.userPassword.trim()){
              this._getProjectList()
              this._getUsers()
              this.$router.push({path:"/main"})
              this.userEmail=''
              this.userPassword=''
          }
          if(!this.userEmail.trim()){
              this.uEmailErrorText='账号不能为空'
          }
          if(!this.userPassword.trim()){
              this.uPswErrorText='密码不能为空'
          }
          
      },
      toRegister(){
            this.uEmailErrorText=''
            this.uPswErrorText=''
          this.$router.push({path:"/register"})
      },
        _getProjectList(){
            getProject().then(res => {
            if(res.code === 0){
                this.SET_PROLIST(res.data.projectList)
            }
            });
        },        
        _getUsers(){
            getUsers().then(res => {
            if(res.code === 0){
                this.SET_USERS(res.data.users)
            }
            });
        },
    ...mapMutations([
      'SET_PROLIST',
      'SET_USERS'
    ])
  },
  watch:{
      userEmail(newv){
          if(newv.trim()){
              this.uEmailErrorText=''
          }
      },
      userPassword(newv){
          if(newv.trim()){
              this.uPswErrorText=''
          }
      }
  }
};
</script>

<style lang="less">
@import "../../common/less/index.less";
@import "../../common/less/enter.less";
.login_btns{
    width:55%;
    margin: 20px auto;
    display: flex;
    justify-content: space-between;

    .mu-button{
        color:@word_color_3;
    }

    .mu-icon{
        margin-right: 5px;
    }
}
</style>



