<template>
  <div class="nav">
    <mu-drawer :open.sync="isNavOpen" :docked="false" :left="true" class="nav_body">
        <mu-list>
            <mu-list-item button @click="exit()">
              <mu-list-item-action>
                <mu-icon value="power_settings_new" color="secondary"></mu-icon>
              </mu-list-item-action>
              <mu-list-item-title>退出账号</mu-list-item-title>
            </mu-list-item>
        </mu-list>
    </mu-drawer>
    <div class="nav_bg" @click="offNav()" v-show="isNavOpen"></div>

    <!-- 点击“退出账号”弹出框 -->

    <DialogText 
    :openTextDialog="openExitDialog" :dialogMessage="'确定退出账号回到登录页吗？'"
    @closeTextDialog="closeExitDialog" @confirmBtn="exitConfirmBtn" class="nav_dialog"></DialogText>
  </div>
</template>

<script type="text/ecmascript-6">

import DialogText from "../dialog/DialogText.vue";
import { mapGetters, mapMutations } from "vuex";

export default {
  name: "Nav",
  data() {
    return {
      openExitDialog:false
    };
  },
  components:{
    DialogText
  },
  computed: {
    ...mapGetters([
      'isNavOpen'
    ])
  },
  methods:{
    offNav(){
        this.SET_NAVOPEN(false)
    },
    exit(){
      this.openExitDialog=true
    },
    closeExitDialog(){
      this.openExitDialog=false
    },
    exitConfirmBtn(){
       this.openExitDialog=false
       this.$router.push({path:"/login"})
       this.SET_NAVOPEN(false)
    },
    ...mapMutations([
      'SET_NAVOPEN'
    ])
  }
};
</script>

<style lang="less">
.mu-overlay{
    z-index: 20141222!important;
}
.nav_bg{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 20141223;
    width:100%;
    height: 100%;
}
.nav_body{
  width:300px;
}
@media screen and (max-width:414px) {
  .nav_body{
    width:200px;
  }
  .nav{
    .mu-item-action{
      min-width:36px;
    }
  }
  .nav_dialog{
    width:60%;
  }
}
</style>



