<template>
  <div class="main">

    <Nav></Nav>
    <div class="app_body" :class="{'active':isNavOpen}">
      <Header class="animated fadeInDown"></Header>
      <AddBtn class="animated fadeInUp"></AddBtn>

      <router-view class="animated fadeInRight"/>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">

import Header from '../header/Header.vue'
import Nav from '../nav/Nav.vue'
import AddBtn from '../addBtn/AddBtn.vue'
import ProjectList from '../projectList/ProjectList.vue'
import { mapGetters, mapMutations } from "vuex";

export default {
name: 'Main',
  data() {
    return {
      
    }
  },
  components: {
    Header,
    Nav,
    AddBtn,
    ProjectList
  },
  computed: {
    ...mapGetters([
      'isNavOpen'
    ])
  },
  methods: {
  }
}
</script>

<style lang="less">

@import "../../common/less/index.less";

.app_body{
  transition: all 0.3s;
  background: #fafafa;
  overflow: hidden;
  &.active{
    transform: translate3d(350px,50px,0);
  }
}
@media screen and (max-width:414px) {
  .app_body{
    &.active{
      transform: translate3d(230px,30px,0);
    }
  }
}
</style>



