<template>
  <div class="avatar">
    <mu-avatar :size="90" class="checked_avatar">
        <img :src="userAva">
    </mu-avatar>
    <mu-sub-header>选择头像</mu-sub-header>
    <div class="avatar_box">
        <mu-avatar :size="70" v-for="(avatar,index) in avatars" :key="index" @click="chooseAvatar(index)">
            <img :src="avatar">
        </mu-avatar>
    </div>
        
    <mu-card-actions>
        <mu-button large color="secondary" @click="avatarNextStep()">下一步</mu-button><br/>
    </mu-card-actions>
  </div>
</template>

<script type="text/ecmascript-6">

export default {
name: 'Avatar',
  data() {
    return {
    }
  },
  props:{
      userAva:{
          type:String,
          default:''
      },
      avatars:{
          type:Array,
          default:[]
      }
  },
  methods:{
    chooseAvatar(index){
        this.$emit('chooseAvatar',index)
    },
    avatarNextStep(){
        this.$emit('avatarNextStep')
    }
  }
}
</script>

<style lang="less">
.checked_avatar {
  margin-top: 30px;
}
.avatar_box {
  .mu-avatar {
    margin: 15px 15px 0 15px;
  }
}
</style>



