<template>
  <div class="register enter">
      <div class="enter_bg animated fadeInDown"></div>
      
    <mu-card class="enter_card animated fadeInUp">
      
        <mu-card-media title="注册" sub-title="REGISTER">
            <img src="../../assets/images/register_img.jpg">
        </mu-card-media>
        <mu-card-text>
            <mu-tabs :value.sync="tabActive" inverse color="secondary" text-color="rgba(0, 0, 0, .54)" indicator-color="secondary" center>
                <mu-tab>账号信息(必填)</mu-tab>
                <mu-tab>头像信息</mu-tab>
                <mu-tab>个人信息(选填)</mu-tab>
            </mu-tabs>
            <div v-if="tabActive === 0">
                <mu-text-field v-model="userEmail" label="账号（邮箱）" :error-text="uEmailErrorText" color="secondary" label-float></mu-text-field><br/>
                <mu-text-field v-model="userName" label="姓名" :error-text="uNameErrorText" color="secondary" label-float ></mu-text-field><br/>
                <mu-text-field v-model="userPassword" label="密码" :error-text="uPswErrorText"
                :action-icon="visibility ? 'visibility_off' : 'visibility'" :action-click="() => (visibility = !visibility)" 
                :type="visibility  ? 'password' : 'text'"  color="secondary" label-float></mu-text-field><br/>
            
                <mu-text-field v-model="repeatPassword" label="再输一次密码" :error-text="uRepPswErrorText"
                :action-icon="visibility ? 'visibility_off' : 'visibility'" :action-click="() => (visibility = !visibility)" 
                :type="visibility  ? 'password' : 'text'"  color="secondary" label-float></mu-text-field><br/>
            
                <mu-card-actions>
                    <mu-button large  color="secondary" @click="nextStep()">下一步</mu-button><br/>
                </mu-card-actions>
            </div>
            <div v-if="tabActive === 1">
              <Avatar :userAva="userAva" :avatars="avatars" @chooseAvatar="chooseAvatar" @avatarNextStep="nextStep"></Avatar>
            </div>
            <div class="register_personal" v-if="tabActive === 2">
                
                <mu-text-field v-model="userIdcard" label="身份证号" color="secondary" label-float></mu-text-field><br/>
                <mu-date-input v-model="userBirthday" label="出生日期" color="secondary" display-color="secondary" underline-color="secondary" label-float ></mu-date-input><br/>
                <mu-text-field v-model="userAge" label="年龄" color="secondary" label-float suffix="岁"></mu-text-field><br/>

                <!-- 地址选择三级联动组件 -->
                <AddressCheck :idCardAreaId="idCardAreaId" @getUserAddress="getUserAddress"></AddressCheck>
                <div class="filled_address" v-if="userAddress">
                  <span>已填地址：</span>{{userAddress}}
                </div>

                <mu-card-actions> 
                    <mu-button large color="secondary" @click="toLogin()">注册</mu-button><br/>
                </mu-card-actions>
            </div>

        </mu-card-text>
    </mu-card>
  </div>
</template>

<script type="text/ecmascript-6">

import Avatar from "../avatar/Avatar.vue"
import AddressCheck from "../addressCheck/AddressCheck.vue"
import * as ApiPath from "../../api/apiPath.js"

export default {
  name: "Login",
  data() {
    return {
      userEmail: "",
      userName: "",
      userPassword: "",
      repeatPassword: "",
      userIdcard: "",
      userBirthday: "",
      userAge: "",
      userAddress: "",
      visibility: true,
      tabActive: 0,
      idCardAreaId:'',
      avatars:ApiPath.avatar,
      userAva:'',
      uEmailErrorText:'',
      uNameErrorText:'',
      uPswErrorText:'',
      uRepPswErrorText:''
    };
  },
  components: {
    Avatar,
    AddressCheck
  },
  computed:{
  },
  methods: {
    nextStep() {
      if(this.userEmail.trim() && this.userName.trim() && this.userPassword.trim() && this.userPassword===this.repeatPassword){
        this.tabActive++;
      }
      this.isFillIn()
    },
    toLogin() {
      if(this.userEmail.trim() && this.userName.trim() && this.userPassword.trim() && this.userPassword===this.repeatPassword){
        this.$router.push({ path: "/login" });
      }else{
        this.tabActive=0
        this.isFillIn()
      }
    },
    getAge(birthday) {
      let now = new Date();
      let year = now.getFullYear();
      let month = now.getMonth()+1; 
      let day = now.getDate(); 

      let birth_year = birthday.substring(0,4); 
      let birth_month  = birthday.substring(5,6); 
      let birth_day  = birthday.substring(7,8); 

      let offDays = this.getOffDays(new Date(birth_year,birth_month,birth_day),new Date(year,month,day)); 
      this.userAge = parseInt(offDays /365.25);
    },
    getOffDays(startDate, endDate) {
      //得到时间戳相减 得到以毫秒为单位的差
      let mmSec = endDate.getTime() - startDate.getTime();
      //单位转换为天并返回
      return mmSec / 3600000 / 24;
    },
    chooseAvatar(index){
      this.userAva=this.avatars[index]
    },
    getUserAddress(userAddress){
      this.userAddress=userAddress
    },
    isFillIn(){
      if(!this.userEmail.trim()){
        this.uEmailErrorText='账号（邮箱）不能为空'
      }
      if(!this.userName.trim()){
        this.uNameErrorText='姓名不能为空'
      }
      if(!this.userPassword.trim()){
        this.uPswErrorText='密码不能为空'
      }
      if(this.userPassword!==this.repeatPassword){
        this.uRepPswErrorText='两次输入密码不一致，请重新输入'
      }
    }
  },
  watch:{
    userIdcard(){
      if (this.userIdcard === "") {
        return;
      }
      if (this.userIdcard.length === 18) {
        this.userBirthday = this.userIdcard.substring(6, 14);
        this.getAge(this.userBirthday);
        this.idCardAreaId = this.userIdcard.substring(0, 6);
      }
    },
    userEmail(newv){
        if(newv.trim()){
            this.uEmailErrorText=''
        }
    },
    userName(newv){
        if(newv.trim()){
            this.uNameErrorText=''
        }
    },
    userPassword(newv){
        if(newv.trim()){
            this.uPswErrorText=''
        }
    },
    repeatPassword(newv){
        if(newv.trim() || newv === this.userPassword){
            this.uRepPswErrorText=''
        }
    }
  },
  created(){
    this.userAva=this.avatars[0]
  }
};
</script>

<style lang="less">
@import "../../common/less/index.less";
@import "../../common/less/enter.less";
.login_btns {
  width: 55%;
  margin: 20px auto;
  display: flex;
  justify-content: space-between;

  .mu-button {
    color: @word_color_3;
  }

  .mu-icon {
    margin-right: 5px;
  }
}
.filled_address{
  color: @word_color_3;
  width: 80%;
  margin: 15px auto 0 auto;
  text-align: left;
  font-size: 12px;

  span{
    color:@word_color_2;
  }
}
</style>



